                                <li class="breadcrumb-item active" aria-current="page"><span>Balance</span></li>
                            </ol>
                        </nav>
                    </div>
                </li>
            </ul>

            <h3 class="text-center">LEAVE ENCASHMENT REQUESTS</h3>

            <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto">

                           <button class="btn btn-success "
                             @click="printEmployees(items)" 
                            >                           
                              <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
  <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
  <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
</svg>
</button>
 
                        </div>
                    </div>  

                    <!-- add leave starts -->

                     <!-- add leave ends -->
      
        <div class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="employeesList" :columns="columns" :options="table_option">
                            <template #status="props">
                                <span class="badge inv-status" :class="'badge-' + props.row.status.color">
                                    {{ props.row.status.name }}
                                </span>
                            </template>
                            <template #actions="props">

  <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-eye text-primary" viewBox="0 0 16 16"
   data-bs-toggle="modal" data-bs-target="#view-leaves"
   @click="selected_encashment = props.row"
  >
    <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z"/>
    <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0"/>
  </svg>
                              
<!-- modal starts -->


 
                                
                            </template>
                           
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>
    </div>

    <!-- Modal -->
<div class="modal fade mymodal" id="view-leaves" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" data-bs-backdrop="false">
    <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">{{selected_encashment?.employee?.toUpperCase()}} LEAVE ENCASHMENT REQUEST</h5>
                <button type="button" data-dismiss="modal" data-bs-dismiss="modal" aria-label="Close" class="btn-close" id="close-approval"></button>
            </div>
            <div class="modal-body">
        <div class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
          <div class="panel p-0">
                    <div class="custom-table table3">      
<table class="table table-bordered" v-if="selected_encashment">
  <thead class="table-light">
    <tr>
      <th>Date Requested</th>
      <th>Amount</th>
      <th>Status</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>{{ selected_encashment.date_requested }}</td>
      <td>{{ selected_encashment.amount }}</td>
      <td>
        <span class="badge inv-status" :class="'badge-' + selected_encashment.status.color">
          {{ selected_encashment.status.name }}
        </span>
      </td>
    </tr>
    <tr v-if="selected_encashment.remark">
        <td colspan="4">Remark : {{ selected_encashment.remark }}</td>
    </tr>
  </tbody>
</table>
</div>
</div>
                
  <div class="d-flex justify-content-center mb-3 mt-2" v-if="selected_encashment.status_id !=3">
    <div class="btn-group" v-if="user_permissions.includes('can_approve_encashment')">
      <button class="btn btn-primary me-2" @click="status = 3" :disabled="spinner">Approve</button>
      <button class="btn btn-danger me-2" @click="status = 4" :disabled="spinner">Reject</button>
      <button class="btn btn-warning me-2" @click="status = 17" :disabled="spinner">Hold Request</button>
      <button class="btn btn-info me-2" @click="status = 14" :disabled="spinner">Remark</button>
    </div>
  </div>

  <div v-if="status" class="mb-2 text-center">
    <span
      class="badge rounded-pill"
      :class="{
        'bg-danger': status == 4,
        'bg-success': status == 3,
        'bg-warning': status == 17,
        'bg-info': status != 3 && status != 4
      }"
      style="font-size: 1rem;"
    >
      {{
        status == 3
          ? 'Approve Request'
          : status == 17
            ? 'Hold Request'
          : status == 4
          ? 'Reject Request'
          : 'Fill Remark'
      }}
    </span>
  </div>

  <div class="input-group" v-if="status">
    <input
      type="text"
      class="form-control"
      :placeholder="status == 3 ? 'Enter Approval Remark' : status == 4 ? 'Enter Rejection Remark' : 'Enter Remark'"
      v-model="remark"
    >
    <button class="btn btn-primary" type="button" :disabled="spinner" @click="updateEncashmentStatus()">
      <span v-if="spinner" v-html="loadingSpinner()"></span>
      <span v-else>Save</span>
    </button>
  </div>

            </div>
        </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn bg-danger text-white" data-dismiss="modal" data-bs-dismiss="modal"><i class="flaticon-cancel-12"></i>CLOSE</button>
            </div>
        </div>
    </div>
</div>
 <!-- modal ends -->

</template>
<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>
<script setup>
    import { onMounted, ref ,inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    useMeta({ title: 'Custom Table' });
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    const axiosInstance = axios.create({
       baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const spinner = ref(false)
    const remark = ref('')
    const printEmployees = inject('printEmployees')
    const showMessage = inject('showMessage')
    const showError = inject('showError')
    const formatNumber = inject('formatNumber')
    const formatDate = inject('formatDate')
    const name = ref('');
    const columns = ref(['Sno', 'employee','date_requested','amount','status','actions']);
    const columns2 = ref(['Sno', 'start_date','end_date','leave_days','status','remark','actions']);
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const status = ref('')
    const items = ref([]);
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 , 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'service_name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });


       const addLeave = ref(false)

const selected_encashment = ref('')

    const employeesList = ref([])

const getEncashments = async () => {
       const payload = [2,4,17,14]
      const response = await axiosInstance.get('leave-encashments', {
         params: {
            status_id: payload
         }})
  employeesList.value = response.data.map((item, index) => {
    return {
      ...item,
      Sno: index + 1,
      employee : item.employee.name,
      date_requested : formatDate(item.created_at),
      amount : formatNumber(item.amount)
    }
  })
}


    const updateEncashmentStatus = async()=>{
        const payload = {
            status_id : status.value,
            remark : remark.value
        }

         try{
           const response = await axiosInstance.put(`leave-encashments/${selected_encashment.value.id}`,payload)
           showMessage('Confirmed')
           document.getElementById('close-approval').click()
           getEncashments()
           remark.value = ''
           status.value = ''
   }catch(error){
    showError(error.message)
    console.log(error)
   }finally{

   }

    }


    onMounted(() => {
        getEncashments()
    });
</script>
